import React, { useState, useEffect } from "react";
import { LineChart } from 'react-chartkick';
import { Findings } from "./findings";
import 'chart.js';

export const Analysis = ({ propStateData }) => {
	const [stateData, setStateData] = useState({ ...propStateData });
	const [state, setState] = useState("California");
	const [county, setCounty] = useState("Alameda");
	const [stateLineData, setStateLineData] = useState({});
	const [countyLineData, setCountyLineData] = useState({});
	const [selection, setSelection] = useState("marriage");

	useEffect(() => {
		if (Object.keys(stateLineData).length === 0) {
			displayStateMarriageData(state);
			displayCountyMarriageData(state, county);
		}
	}, []);

	const displayStateLandData = (stateName) => {
		const stateLineData = {};
		Object.keys(stateData[stateName].averageYearlyValue).map((year) => {
			stateLineData[year] = stateData[stateName].averageYearlyValue[year].value;
		})
		setStateLineData(stateLineData);
		return;
	}

	const displayStateMarriageData = (stateName) => {
		const stateLineData = [];
		const marriages = {
			name: "Marriages",
			data: {}
		}
		const divorces = {
			name: "Divorces",
			data: {}
		}
		Object.keys(stateData[stateName].marriages).map((year) => {
			marriages.data[year] = stateData[stateName].marriages[year];
		});
		Object.keys(stateData[stateName].divorces).map((year) => {
			divorces.data[year] = stateData[stateName].divorces[year];
		});
		stateLineData.push(marriages);
		stateLineData.push(divorces);
		setStateLineData(stateLineData);
		return;
	}

	const displayCountyLandData = (stateName, countyName) => {
		const countyLineData = {};
		Object.keys(stateData[stateName].counties[countyName].averageYearlyValue).map((year) => {
			countyLineData[year] = stateData[stateName].counties[countyName].averageYearlyValue[year];
		});
		setCountyLineData(countyLineData);
		return;
	}

	const displayCountyMarriageData = (stateName, countyName) => {
		const countyLineData = [];
		const marriages = {
			name: "Marriages",
			data: {}
		}
		const divorces = {
			name: "Divorces",
			data: {}
		}
		Object.keys(stateData[stateName].counties[countyName].marriages).map((year) => {
			marriages.data[year] = stateData[stateName].counties[countyName].marriages[year];
		});
		Object.keys(stateData[stateName].counties[countyName].divorces).map((year) => {
			divorces.data[year] = stateData[stateName].counties[countyName].divorces[year];
		});
		countyLineData.push(marriages);
		countyLineData.push(divorces);
		setCountyLineData(countyLineData);
		return;
	}


	const isSelected = (regionName) => {
		return (regionName === state || regionName === county) ? " bg-light-gray " : "";
	}

	const sideNav = (region, isCounty) => {
		return (
			<div className={"flex-column h-100 mb4 f4"}>
				<div className={"h-100"}>
					{Object.keys(region).sort().map((regionName, index) => {
						return (
							<div key={index} className={"pa2 tl bb " + isSelected(regionName)} onClick={() => {
								if (selection === "land") {
									if (isCounty) {
										setCounty(regionName);
										displayCountyLandData(state, regionName);
									} else {
										setState(regionName);
										setCounty(Object.keys(stateData[regionName].counties).sort()[0]);
										displayStateLandData(regionName);
										displayCountyLandData(regionName, Object.keys(stateData[regionName].counties).sort()[0]);
									}
								} else if (selection === "marriage") {
									if (isCounty) {
										setCounty(regionName);
										displayCountyMarriageData(state, regionName);
									} else {
										setState(regionName);
										setCounty(Object.keys(stateData[regionName].counties).sort()[0]);
										displayStateMarriageData(regionName);
										displayCountyMarriageData(regionName, Object.keys(stateData[regionName].counties).sort()[0])
									}
								}
							}}>
								{regionName}
							</div>
						)
					})}
				</div>
			</div>
		)
	}

	return (
		<div>
			<div>
				<div className="flex tc justify-center mv4">
					<div className={"w-30 ba mh3 pa2 f3" + (selection === "marriage" ? " bg-light-gray " : "")} onClick={() => {
						setSelection("marriage")
						displayStateMarriageData(state);
						displayCountyMarriageData(state, county);
					}}> Marriage Info</div>
					<div className={"w-30 ba mh3 pa2 f3" + (selection === "land" ? " bg-light-gray " : "")} onClick={() => {
						setSelection("land")
						displayStateLandData(state);
						displayCountyLandData(state, county);
					}}> Land Value </div>
				</div>
			</div>
			<div className="flex mb4">
				<Findings />
				<div className="w-70">
					<div className={"f3 mh2 mt4 tc mb4 justify-center"}>State: {state}</div>
					<div className="flex mb5 overflow-hidden" style={{ height: "300px" }}>
						<div className="w-20 overflow-y-scroll" style={{ height: "300px" }}>
							{sideNav(stateData, false)}
						</div>
						<div className="w-80">
							<div className="flex w-100">
								<div className="w-90 mh3">
									<div className={""}>
										{<LineChart data={stateLineData} />}
									</div>
								</div>
							</div>
						</div>
					</div>
					<div className="f3 mb3 tc mt2 justify-center">{state} County: {county}</div>
					<div className="flex overflow-hidden" style={{ height: "400px" }}>
						<div className="w-20 overflow-y-scroll" style={{ height: "400px" }}>
							{sideNav(stateData[state].counties, true)}
						</div>
						<div className="w-80">
							<div className="flex w-100">
								<div className={"w-90 mh3"}>
									{<LineChart data={countyLineData} />}
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};
